.container {
    text-align: center;
}

.filters {
    display: flex;
    flex-wrap: wrap; /* Allows items to wrap to the next row */
    justify-content: center;
    margin-bottom: 20px;
}

.dropdown-label {
    margin: 10px 15px; /* Adds spacing between rows and items */
    font-size: 0.8rem;
}

.dropdown-label select {
    font-size: 0.8rem;
    margin-left: 5px;
    text-align: center;
    padding: 4px;
    height: 28px;
    line-height: 1.2;
    border-radius: 5px;
    border: 1px solid #ecc30b;
}

table {
    margin: 0 auto;
    border-collapse: collapse;
}

table th, table td {
    border: 1px solid #ccc;
    padding: 8px;
    text-align: center;
}

.pipe-image {
    max-width: 100%;
    height: auto;
    display: block;
    margin: auto; /* Center the image horizontally */
}

.specs-table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 10px; /* Space between cells */
}

.specs-table td {
    padding: 5px; /* Reduced padding to decrease box height */
    border: 1px solid #5a5a5a; /* Optional border around cells */
    text-align: center; /* Center text in each cell */
    border-radius: 10px;
    font-size: 0.8rem; /* Adjust font size for compact design */
}

